}

.border-t-transparent {
  border-top-color: transparent;
}

.border-b {
  border-bottom-width: 1px;
}

.shadow-lg {
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

.shadow-xl {
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}

.backdrop-blur-sm {
  backdrop-filter: blur(4px);
}

.bg-white\/80 {
  background-color: rgba(255, 255, 255, 0.8);
}

.sticky {
  position: sticky;
}

.top-0 {
  top: 0;
}

.z-50 {
  z-index: 50;
}

.grid {
  display: grid;
}

.grid-cols-1 {
  grid-template-columns: repeat(1, minmax(0, 1fr));
}

.gap-6 {
  gap: 1.5rem;
}

.transition-colors {
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out;
}

.transition-all {
  transition: all 0.15s ease-in-out;
}

.duration-200 {
  transition-duration: 0.2s;
}

.hover\:bg-red-600:hover {
  background-color: #dc2626;
}

.hover\:from-green-700:hover {
  background: linear-gradient(90deg, #15803d, #166534);
}

.hover\:to-green-800:hover {
  background: linear-gradient(90deg, #15803d, #166534);
}

.hover\:scale-105:hover {
  transform: scale(1.05);
}

.transform {
  transform: translateX(0) translateY(0) rotate(0) skewX(0) skewY(0) scaleX(1) scaleY(1);
}

.focus\:ring-2:focus {
  box-shadow: 0 0 0 2px rgba(16, 163, 74, 0.5);
}

.focus\:ring-green-500:focus {
  box-shadow: 0 0 0 2px rgba(16, 163, 74, 0.5);
}

.focus\:border-transparent:focus {
  border-color: transparent;
}

.disabled\:opacity-50:disabled {
  opacity: 0.5;
}

.disabled\:cursor-not-allowed:disabled {
  cursor: not-allowed;
}

.disabled\:transform-none:disabled {
  transform: none;
}

.animate-spin {
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

/* Responsive classes for medium screens and up */
@media (min-width: 768px) {
  .md\:grid-cols-3 {
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }
}

/* Additional utility classes */
.cursor-pointer {
  cursor: pointer;
}

.cursor-not-allowed {
  cursor: not-allowed;
}

.capitalize {
  text-transform: capitalize;
}

.select-none {
  user-select: none;
}

/* Custom enhancements */
.min-h-\[calc\(100vh-80px\)\] {
  min-height: calc(100vh - 80px);
}

/* Make sure forms look good */
input,
button {
  outline: none;
}

input:focus {
  border-color: #16a34a !important;
  box-shadow: 0 0 0 2px rgba(22, 163, 74, 0.1) !important;
}